import React from "react";

function Landing({handleBookClick}){
    return (
        <div className='landing-container' id='landing'>
            {/*<div className='landing'>*/}
                <div className='header'>Odzyskaj zdrowie - odzyskaj życie
                </div>
                <div className='subheader'>Profesjonalna rehabilitacja dla każdego – nowoczesne metody, indywidualne podejście, skuteczne efekty</div>
                <button className='button' onClick={handleBookClick}>Umów wizytę</button>
            {/*</div>*/}
        </div>
    )
}

export default Landing;